 financial education/inclusion, digital literacy, promoting income generating initiatives and entrepreneurships skills among women and young people. This is a continuous project that has been running with support from partners and individual well-wishers. The project has since 2015 been implemented in the districts of Moyo, Wakiso, Mbarara, Hoima, Bushenyi, Kampala, Rukungiri and currently active in Kasese district. We have been able to reach over 15000 people. ")])],1)])],1)],1),_c('v-col',{attrs:{"cols":"12","sm":"12","md":"3","lg":"3"}},[_c('v-card',{staticClass:"mx-auto"},[_c('v-img',{attrs:{"src":require("../assets/resilence.jpg"),"height":"200px"}}),_c('v-card-title',{staticStyle:{"font-size":"1rem"}},[_vm._v(" Strengthening Resilience & Economic Empowerment among PWDs in the face of Covid-19 pandemic ")]),_c('v-card-actions',[_c('v-btn',{attrs:{"color":"orange lighten-2","text":""}},[_vm._v(" Read more ")]),_c('v-spacer'),_c('v-btn',{attrs:{"icon":""},on:{"click":function($event){_vm.show2 = !_vm.show2}}},[_c('v-icon',[_vm._v(_vm._s(_vm.show2 ? "mdi-chevron-up" : "mdi-chevron-down"))])],1)],1),_c('v-expand-transition',[_c('div',{directives:[{name:"show",rawName:"v-show",value:(_vm.show2),expression:"show2"}]},[_c('v-divider'),_c('v-card-text',[_vm._v(" In Uganda, 16.4% of the population lives with some form of disability, Kasese municipality accounts for 22.6% of all persons with disabilities (PWDs) living in Kasese district. People with disabilities have hopes and dreams just as any other people. The reality, however, is that many in Kasese district remain marginalized and excluded. Covid-19 caused a huge impact on PWDs. The situation became worse for PWDs in the district experiencing frequent occurrences of environmental natural calamities such as flash floods and landslides leaving many PWDs displaced. Many slip into a survival mode as a result of a breakdown in their livelihoods, further increasing risks of gender-based violence. The Strengthening Resilience and Economic Empowerment among PWDs is a Global Greengrants Fund supported project. The projects objectives are to; mobilize PWDs for economic recovery and access to empowerment skills to reduce their vulnerability; build the capacities of PWDs in human rights so they are aware of and able to demand their rights; and assess the participation of PWDs in development projects to inform awareness campaigns and reduce stigma and discrimination towards PWDs. Leveraging on partnerships, together with Kasese District Association of Persons with Disabilities (KADUPEDI) we have mobilized groups of women with disabilities and trained them financial literacy, held community dialogues and build the capacity of board members of KADUPEDI for better project delivery. ")])],1)])],1)],1),_c('v-col',{attrs:{"cols":"12","sm":"12","md":"3","lg":"3"}},[_c('v-card',{staticClass:"mx-auto"},[_c('v-img',{attrs:{"src":require("../assets/menstral.jpg"),"height":"200px"}}),_c('v-card-title',{staticStyle:{"font-size":"1rem"}},[_vm._v(" Ending Menstrual Stigma among teenage girls and women with disabilities affected by floods amidst Covid-19 ")]),_c('v-card-actions',[_c('v-btn',{attrs:{"color":"orange lighten-2","text":""}},[_vm._v(" Read more ")]),_c('v-spacer'),_c('v-btn',{attrs:{"icon":""},on:{"click":function($event){_vm.show3 = !_vm.show3}}},[_c('v-icon',[_vm._v(_vm._s(_vm.show3 ? "mdi-chevron-up" : "mdi-chevron-down"))])],1)],1),_c('v-expand-transition',[_c('div',{directives:[{name:"show",rawName:"v-show",value:(_vm.show3),expression:"show3"}]},[_c('v-divider'),_c('v-card-text',[_vm._v(" While water, sanitation and hygiene (WASH) continue to be the first line of defense against COVID-19. It’s widely recognized that access to clean, running water and soap for handwashing is a critical need that must be met in our global response to the pandemic. Women and adolescent girls who are internally displaced living in the aftermath of a natural disaster like the Kasese floods, face an added burden in managing their periods during the pandemic, but this is still a challenge, with now current reports of teenage girls exchanging pads for sex, this puts disabled girls in double vulnerability to violence. With support from Raising Teenagers Uganda, vulnerable teenage girls and women including those with disabilities from the three divisions of Nyamwamba, Bulembya and Central division from Kasese municipality who were severely affected by the floods continue to benefit from the menstrual material relief program. We have so far provided quality reusable sanitary pads, soap, and under wears, and further created awareness on menstrual hygiene among 200 teenage girls including those with disabilities. ")])],1)])],1)],1),_c('v-col',{attrs:{"cols":"12","sm":"12","md":"3","lg":"3"}},[_c('v-card',{staticClass:"mx-auto"},[_c('v-img',{attrs:{"src":require("../assets/whatsap.jpg"),"height":"200px"}}),_c('v-card-title',{staticStyle:{"font-size":"1rem"}},[_vm._v(" Emergency response to climate change effects e.g Food relief to floods affected people-Kasese-Uganda ")]),_c('v-card-actions',[_c('v-btn',{attrs:{"color":"orange lighten-2","text":""}},[_vm._v(" Read more ")]),_c('v-spacer'),_c('v-btn',{attrs:{"icon":""},on:{"click":function($event){_vm.show4 = !_vm.show4}}},[_c('v-icon',[_vm._v(_vm._s(_vm.show4 ? "mdi-chevron-up" : "mdi-chevron-down"))])],1)],1),_c('v-expand-transition',[_c('div',{directives:[{name:"show",rawName:"v-show",value:(_vm.show4),expression:"show4"}]},[_c('v-divider'),_c('v-card-text',[_vm._v(" Disasters and complex emergencies often have a significant impact on the environment. Emergencies are urgent situations that occur and cause human suffering or imminently threaten lives or livelihoods. At YAGANET, we bridge short-term humanitarian and contribute to long-term development approaches to promote disaster resilience and sustainable development, and address the needs of the most vulnerable communities. In Kasese district, For the last seven years, the district has experienced repeated floods and landslides as in 2013, 2014, 2015, 2020, and 2021. This has been as a result of the heavy rains that have led to the breaking of the banks of the major rivers of Lhubiriha, Mubuku, Nyamwamba, Nyamugasani, Ssebwe, Rwembyo and Thaku among others, which flood simultaneously at the same time from the Rwenzori Mountain. Since 2020, YAGANET has received support from partners like ACRE Uganada/PennyAppeal which supported 200 floods affected households with food packs that included maize flour, beans, cooking oil, rice, sugar, and soap. And recently in 2021 YAGANET has received support from Joy for Children Uganda and supplied relief materials including maize floor, beans, soap and also sanitary pads for teenage girls to over 100 beneficiaries. With support from Raising Teenagers Uganda supported vulnerable teenage girls including those living with disabilities with reusable sanitary pads from communities affected by floods. ‘The reward of feeding the poor is immense, and Bible tells us that the righteous "),_c('b',[_vm._v(" “give food, out of love for Him, to the poor and the orphan and the captive” ")])])],1)])],1)],1)],1),_c('v-row',[_c('v-col',{staticStyle:{"background":"#edf9ee"}},[_c('p',{staticStyle:{"font-family":"Roboto","font-size":"24px","font-style":"normal","font-weight":"300","line-height":"28px","letter-spacing":"0em","text-align":"left","padding-left":"10px","margin-right":"30px","padding-top":"80px"}},[_vm._v(" YAGANET was founded in 2016 as a social enterprise working to see inclusive financial and non-financial services, delivering and spreading financial literacy knowledge, business and life skills training services to under-served young people aged 8-24yrs and women between 24-35yrs while leveraging on partnerships and bringing economies of scale. ")])]),_c('v-col',{staticClass:"about-col"},[_c('v-img',{attrs:{"max-width":"860","src":require("../assets/young-about.webp")}})],1)],1)],1),_c('br')],1)}
